import React, { Component } from 'react'
import apiTool from '../../command/apiTool';
import {Button} from 'antd'
import styles  from './index.less'
import Link from 'next/link'
import createDva from '../../command/createDva';
import FormModal from '../../components/FormModal/index';

@createDva([])
export default class Index extends Component {

  constructor(props) {
    super(props);
    this.formModal = createDva(['testModal'])(FormModal)
  }
  
  onTest1 = () =>{
    apiTool.showModal(this,'testModal')
  }

  componentDidUpdate(prevProps, prevState) {
    console.log('输出当前刷新')  
  }

  onUpdate = () =>{
    this.forceUpdate()
  }

  render() {
    const FormModalView = this.formModal
    return (
      <div className={styles.main}>
        <Button onClick={this.onTest1}>点击显示modal</Button>
        <Button onClick={this.onUpdate}>点击刷新</Button>
        <FormModalView/>
      </div>
    )
  }
}
